@import '../../styles/variables';

label {
    font-size: 12px;
    font-weight: 700;
    color: $Dark-navy;
}

input {
    width: 100%;
    box-sizing: border-box;
    padding: 16px 24px;
    margin-top: 8px;
    border-radius: 8px;

    &.invalid {
        border: solid $Tomato;
        background-color: rgba($Tomato, 0.15);
        color: $Tomato;
    }

}

.label-container {
    display: flex;
    justify-content: space-between;
}

.error-message {
    text-align: right;
    color: $Tomato;
}




